<template>
  <div>
    <div class="card" :style="this.cardStyle">
      <img class="card-img-top" :src="require('../assets/' + src)" alt="We couldn't find the picture, sorry :(">
      <div class="card-body">
        <h5 class="card-title">{{ title }}</h5>
        <p class="card-text" :id="title"></p>
        <router-link :to="link">
          <button v-if="this.atClick!=null"  class="btn btn-dark" @click="addToCart()">{{ btnTxt }}</button>
          <button v-else  class="btn btn-dark">{{ btnTxt }}</button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import router from '@/router';

export default {
  name: 'Card',
  props: {
    src: String,
    title: String,
    description: String,
    price: 0,
    link: String,
    btnTxt: String,
    cardStyle: String,
    atClick: String
  },

  data(){
    return{cardSt: "width: 18vw;"}
  },

  computed: {
    ...mapState([
        'shoppingCartTotal',
        'token'
    ]),
  },

  mounted() {
    if (!(this.cardStyle == null || this.cardStyle == undefined))
      this.cardSt = this.cardStyle;
      document.getElementById(this.title).innerHTML += this.description;
  },

  methods: {
    ...mapActions([
      'setShoppingCart',
      'updateShoppingCart'
    ]),

    addToCart(){
      if (this.token){
        if (this.shoppingCartTotal!=null){
          this.updateShoppingCart(this.price);
        }
        else{
          this.setShoppingCart(this.price);
        }
      }
      else{
        alert("You're not logged in!");
        //router.push('/login');
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
