<template>
    <div class="lenses">
        <Header title="Lenses:"/>
        <SingleLens v-for="lens in lensesShown" :key="lens.id" :element="lens"/>
        <div class="text-center">
            <a href="#" class="btn btn-dark" @click="prev()" style="margin: 1vw;">&lt&lt</a>
            <a href="#" class="btn btn-dark" @click="next()" style="margin: 1vw;">>></a>
        </div>
    </div>
</template>

<script>
import Header from '../components/Header.vue'
import Card from '../components/Card.vue'
import SingleLens from '../components/SingleLens.vue'
import {mapActions, mapState} from 'vuex';

export default {
name: 'Lenses',
components: {
    Header,
    Card,
    SingleLens
},

data() {
    return{
        current: 0,
        lensesPerTab: 3,
        lensesShown: [],
    }
},

computed: {
    ...mapState([
        'lenses'
    ])
},

mounted() {
    this.fetchLenses();
},

watch: {
    lenses(nVal) {
        this.lensesShown = this.lenses.slice(0, 3);
    } 
},

methods: {
    ...mapActions([
        'fetchLenses'
    ]),

    prev(){
        if (this.current > 0)
        {
            this.current--;
            this.lensesShown = this.lenses.slice(this.current*this.lensesPerTab, (this.current+1)*this.lensesPerTab);
        }
    },

    next(){
        if ((this.current+1) * this.lensesPerTab < this.lenses.length)
        {
            this.current++;
            this.lensesShown = this.lenses.slice(this.current*this.lensesPerTab, (this.current+1)*this.lensesPerTab);
        }
    }
}
}
</script>

