<template>
    <div class="cameras">
        <Header title="Cameras:"/>
        <SingleCamera v-for="camera in camerasShown" :key="camera.id" :element="camera"/>
        <div class="text-center">
            <a href="#" class="btn btn-dark" @click="prev()" style="margin: 1vw;">&lt&lt</a>
            <a href="#" class="btn btn-dark" @click="next()" style="margin: 1vw;">>></a>
        </div>
    </div>
</template>

<script>
import Header from '../components/Header.vue'
import Card from '../components/Card.vue'
import SingleCamera from '../components/SingleCamera.vue'
import {mapActions, mapMutations, mapState} from 'vuex';

export default {
name: 'Cameras',
components: {
    Header,
    Card,
    SingleCamera
},

data() {
    return{
        current: 0,
        camerasPerTab: 3,
        camerasShown: [],
    }
},

computed: {
    ...mapState([
        'cameras'
    ])
},

mounted() {
    this.fetchCameras();
},

watch: {
    cameras(nVal) {
        this.camerasShown = this.cameras.slice(0, 3);
    } 
},

methods: {
    ...mapActions([
        'fetchCameras'
    ]),

    prev(){
        if (this.current > 0)
        {
            this.current--;
            this.camerasShown = this.cameras.slice(this.current*this.camerasPerTab, (this.current+1)*this.camerasPerTab);
        }
    },

    next(){
        if ((this.current+1) * this.camerasPerTab < this.cameras.length)
        {
            this.current++;
            this.camerasShown = this.cameras.slice(this.current*this.camerasPerTab, (this.current+1)*this.camerasPerTab);
        }
    }
}
}
</script>

