<template>
    <div class="filters">
        <Header title="Filters:"/>
        <SingleFilter v-for="filter in filtersShown" :key="filter.id" :element="filter"/>
        <div class="text-center">
            <a href="#" class="btn btn-dark" @click="prev()" style="margin: 1vw;">&lt&lt</a>
            <a href="#" class="btn btn-dark" @click="next()" style="margin: 1vw;">>></a>
        </div>
    </div>
</template>

<script>
import Header from '../components/Header.vue'
import Card from '../components/Card.vue'
import SingleFilter from '../components/SingleFilter.vue'
import {mapActions, mapState} from 'vuex';

export default {
name: 'Filters',
components: {
    Header,
    Card,
    SingleFilter
},

data() {
    return{
        current: 0,
        filtersPerTab: 3,
        filtersShown: [],
    }
},

computed: {
    ...mapState([
        'filters'
    ])
},

mounted() {
    this.fetchFilters();
},

watch: {
    filters(nVal) {
        this.filtersShown = this.filters.slice(0, 3);
    } 
},

methods: {
    ...mapActions([
        'fetchFilters'
    ]),

    prev(){
        if (this.current > 0)
        {
            this.current--;
            this.filtersShown = this.filters.slice(this.current*this.filtersPerTab, (this.current+1)*this.filtersPerTab);
        }
    },

    next(){
        if ((this.current+1) * this.filtersPerTab < this.filters.length)
        {
            this.current++;
            this.filtersShown = this.filters.slice(this.current*this.filtersPerTab, (this.current+1)*this.filtersPerTab);
        }
    }
}
}
</script>

