<template>
  <div class="login">
    <div class="card reg">
      <div class="card-body">
        <div class="container">
            <div class="row">
                <div class="col-lg-12">
                    <h1 class="text-center">Register account</h1>
                        <div class="form-group text-center">
                            <label for="username">Username</label>
                            <input type="text" class="form-control" id="username" aria-describedby="nameHelp" placeholder="username">               
                        </div>
                        <div class="form-group text-center">
                            <label for="password">Password</label>
                            <input type="text" class="form-control" id="password" aria-describedby="nameHelp" placeholder="password">               
                        </div>
                        <div class="form-group text-center">
                            <label for="email">Email</label>
                            <input type="text" class="form-control" id="email" aria-describedby="nameHelp" placeholder="email">               
                        </div>
                        <br>
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" value="" id="admin">
                            <label class="form-check-label" for="admin">
                              Admin
                            </label>
                        </div>
                        <p style="color:red;" id="error"></p>
                        <div class="text-center">
                          <button type="button" id="btn" class="btn btn-dark" @click="register()">Register</button>
                        </div>
                </div>
            </div>
        </div>
      </div>
    </div>
    <!-- Pills content -->
  </div>
</template>

<script>
  import {mapActions, mapState} from 'vuex';
  import router from '@/router';

  export default {
    name: 'Login',
  
    data(){
      return{
        errorMsg: "",
        message: ""
      }
    },

    computed: {
      ...mapState([
          'token',
      ]),
    },

    methods: {
    ...mapActions([
        'setToken'
    ]),

    validateUser(user){
      let valid = true;
      this.errorMsg = "";

      if (user.username.length <= 0){
          valid = false;
          this.errorMsg += "Username can't be empty!<br>";
      }
      if (user.email.length < 5 || !user.email.includes('@')){
          valid = false;
          this.errorMsg += "Email must be at least 5 characters long and be valid.<br>";
      }
      if (user.password.length < 4){
          valid = false;
          this.errorMsg += "Password must be at least 4 characters long.";
      }

      return valid;
    },

    register(){
      const data = {
          username: document.getElementById('username').value,
          email: document.getElementById('email').value,
          password: document.getElementById('password').value,
          admin: document.getElementById('admin').checked
      }

      if (!this.validateUser(data)){
          document.getElementById('error').innerHTML = this.errorMsg;
      }
      else
      {
        fetch("/auth-register",{
            method: 'post',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(data)
        }).then( res => res.json() )
        .then( el => {
            if (el.msg){
                this.message = "";
                if (el.msg.isArray)
                {
                  el.msg.forEach(element => {
                      this.message += element.message;
                  });
                }
                else
                {
                  this.message = el.msg;
                }
                document.getElementById('error').innerHTML = "";
                document.getElementById('error').innerHTML += this.message;
            }
            else{
                document.getElementById('error').innerHTML = "";

                document.getElementById('username').value = "",
                document.getElementById('email').value = "",
                document.getElementById('password').value = "",
                document.getElementById('admin').checked = false

                this.setToken(el.token);
                router.push('/');
            }  
        })
      }
    }
  }
}
</script>

<style>
.reg{
  margin: 5vw;
  margin-left: 20vw;
  margin-right: 20vw;
}

.form-group{

}
</style>
