<template>
    <div class="tripods">
        <Header title="Tripods:"/>
        <SingleTripod v-for="tripod in tripodsShown" :key="tripod.id" :element="tripod"/>
        <div class="text-center">
            <a href="#" class="btn btn-dark" @click="prev()" style="margin: 1vw;">&lt&lt</a>
            <a href="#" class="btn btn-dark" @click="next()" style="margin: 1vw;">>></a>
        </div>
    </div>
</template>

<script>
import Header from '../components/Header.vue'
import Card from '../components/Card.vue'
import SingleTripod from '../components/SingleTripod.vue'
import {mapActions, mapState} from 'vuex';

export default {
name: 'Tripods',
components: {
    Header,
    Card,
    SingleTripod
},

data() {
    return{
        current: 0,
        tripodsPerTab: 3,
        tripodsShown: [],
    }
},

computed: {
    ...mapState([
        'tripods'
    ])
},

mounted() {
    this.fetchTripods();
},

watch: {
    tripods(nVal) {
        this.tripodsShown = this.tripods.slice(0, 3);
    } 
},

methods: {
    ...mapActions([
        'fetchTripods'
    ]),

    prev(){
        if (this.current > 0)
        {
            this.current--;
            this.tripodsShown = this.tripods.slice(this.current*this.tripodsPerTab, (this.current+1)*this.tripodsPerTab);
        }
    },

    next(){
        if ((this.current+1) * this.tripodsPerTab < this.tripods.length)
        {
            this.current++;
            this.tripodsShown = this.tripods.slice(this.current*this.tripodsPerTab, (this.current+1)*this.tripodsPerTab);
        }
    }
}
}
</script>

