<template>
  <div class="app">
    <div>
      <b-navbar toggleable="lg" type="dark">
      <b-navbar-brand to="/">Camera Central</b-navbar-brand>     

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <!-- <b-nav-item href="#">Link</b-nav-item> -->
          <b-nav-item-dropdown text="Shop" right>
            <b-dropdown-item to="/cameras">Cameras</b-dropdown-item>
            <b-dropdown-item to="/lenses">Lenses</b-dropdown-item>
            <b-dropdown-item to="/filters">Filters</b-dropdown-item>
            <b-dropdown-item to="/microphones">Microphones</b-dropdown-item>
            <b-dropdown-item to="/tripods">Tripods</b-dropdown-item>
          </b-nav-item-dropdown>
          <b-nav-item to="/about">About us</b-nav-item>
        </b-navbar-nav>

        <!-- Right aligned nav items -->
        <b-navbar-nav class="ms-auto">
          <b-nav-item-dropdown v-if="token" class="cart" :text="'Cart: ' + (this.shoppingCartTotal || 0) + '$'">
            <b-dropdown-item v-if="this.shoppingCartTotal!=null && this.shoppingCartTotal!=0" @click="addPurchaseF()">Purchase</b-dropdown-item>
            <b-dropdown-item @click="clearShoppingCart()">Clear cart</b-dropdown-item>
          </b-nav-item-dropdown>
          <b-nav-item-dropdown right>
            <!-- Using 'button-content' slot -->
            <template #button-content>
              <em>User</em>
            </template>
              <b-dropdown-item v-if="!token" to="/login">Login</b-dropdown-item>
              <b-dropdown-item v-if="!token" to="/register">Register</b-dropdown-item>
              <b-dropdown-item v-else @click="logout()">Log out</b-dropdown-item>
          </b-nav-item-dropdown>
          <b-nav-form>
            <b-form-input size="sm" class="me-sm-auto" placeholder="Search"></b-form-input>
            <b-button size="sm" class="ms-2 ms-sm-auto d-flex justify-content-center" type="submit">Search</b-button>
          </b-nav-form>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    </div>
    <div>
      <router-view/>
    </div>
  </div>
</template>


<script>
import {mapActions, mapState} from 'vuex';
import router from '@/router';
import io from 'socket.io-client';

export default {
  name: 'App',

  computed: {
    ...mapState([
        'token',
        'shoppingCartTotal',
      ])
  },

  mounted(){
    this.socket = io('ws://127.0.0.1:8080');
    this.setSocket(this.socket);
    this.socket.on('received', msg => {
      this.addQuestion(msg);
    });
  },

  methods: {
    ...mapActions([
      'removeToken',
      'setToken',
      'updateShoppingCart',
      'addPurchase',
      'setSocket',
      'addQuestion'
    ]),

    logout() {
      this.removeToken();
    },

    addPurchaseF(){
      this.addPurchase(this.shoppingCartTotal);
      this.clearShoppingCart();
      router.push("/purchase");
    },

    clearShoppingCart(){
      this.updateShoppingCart(this.shoppingCartTotal * -1);
    }
  }
}
</script>

