<template>
  <div class="login">
    <div class="card log">
      <div class="card-body">
        <div class="container text-center">
            <div class="row">
                <div class="col-lg-12">
                    <h1>User login</h1>
                      <div class="form-group">
                      <label for="username">Username</label>
                      <input type="text" class="form-control" id="username" aria-describedby="nameHelp" placeholder="username">               
                      </div>
                      <div class="form-group">
                          <label for="password">Password</label>
                          <input type="text" class="form-control" id="password" aria-describedby="nameHelp" placeholder="password">               
                      </div>
                      <br>
                      <button type="button" id="btn" class="btn btn-dark" @click="login()">Login</button>
                </div>
            </div>
        </div>
      </div>
    </div>
    <!-- Pills content -->
  </div>
</template>

<script>
  import {mapActions, mapState} from 'vuex';
  import router from '@/router';

  export default {
  name: 'Login',
  
  computed: {
    ...mapState([
        'token',
    ]),
  },

  methods: {
  ...mapActions([
      'setToken'
  ]),

    login(){

      const data = {
        username: document.getElementById('username').value,
        password: document.getElementById('password').value
      }

      fetch("/auth-login",{
          method: 'post',
          headers: {'Content-Type': 'application/json'},
          body: JSON.stringify(data)
      }).then( res => res.json() )
      .then( el => {
          if (el.msg){
              alert(el.msg);
          }
          else{
              document.getElementById('username').value = "",
              document.getElementById('password').value = ""
              
              this.setToken(el.token);
              router.push('/');
          }
      })
    }
  }
}
  
</script>

<style>
.log{
  margin: 5vw;
  margin-left: 20vw;
  margin-right: 20vw;
}
</style>
