<template>
    <div class="microphones">
        <Header title="Microphones:"/>
        <SingleMicrophone v-for="microphone in microphonesShown" :key="microphone.id" :element="microphone"/>
        <div class="text-center">
            <a href="#" class="btn btn-dark" @click="prev()" style="margin: 1vw;">&lt&lt</a>
            <a href="#" class="btn btn-dark" @click="next()" style="margin: 1vw;">>></a>
        </div>
    </div>
</template>

<script>
import Header from '../components/Header.vue'
import Card from '../components/Card.vue'
import SingleMicrophone from '../components/SingleMicrophone.vue'
import {mapActions, mapState} from 'vuex';

export default {
name: 'Microphones',
components: {
    Header,
    Card,
    SingleMicrophone
},

data() {
    return{
        current: 0,
        microphonesPerTab: 3,
        microphonesShown: [],
    }
},

computed: {
    ...mapState([
        'microphones'
    ])
},

mounted() {
    this.fetchMicrophones();
},

watch: {
    microphones(nVal) {
        this.microphonesShown = this.microphones.slice(0, 3);
    } 
},

methods: {
    ...mapActions([
        'fetchMicrophones'
    ]),

    prev(){
        if (this.current > 0)
        {
            this.current--;
            this.microphonesShown = this.microphones.slice(this.current*this.microphonesPerTab, (this.current+1)*this.microphonesPerTab);
        }
    },

    next(){
        if ((this.current+1) * this.microphonesPerTab < this.microphones.length)
        {
            this.current++;
            this.microphonesShown = this.microphones.slice(this.current*this.microphonesPerTab, (this.current+1)*this.microphonesPerTab);
        }
    }
}
}
</script>

