import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cameras: [],
    lenses: [],
    filters: [],
    microphones: [],
    tripods: [],
    token: '',
    shoppingCartTotal: null,
    questions: [],
    socket: null
  },
  
  mutations: {
    setCameras(state, cameras){
      state.cameras = cameras;
    },

    setLenses(state, lenses){
      state.lenses = lenses;
    },

    setFilters(state, filters){
      state.filters = filters;
    },

    setMicrophones(state, microphones){
      state.microphones = microphones;
    },

    setTripods(state, tripods){
      state.tripods = tripods;
    },

    setToken(state, token){
      state.token = token;
      localStorage.token = token;
    },

    removeToken(state){
      state.token = '';
      localStorage.token = '';
    },

    setShoppingCart(state, price){
      state.shoppingCartTotal = price;
    },

    updateShoppingCart(state, price){
      state.shoppingCartTotal = state.shoppingCartTotal+price;
    },

    setQuestions(state, questions){
      state.questions = questions
    },

    addQuestionM(state, question){
      state.questions.push(question);
    },

    setSocket(state,socket){
      state.socket = socket;
    }
  },

  actions: {
    fetchCameras({commit}){
      fetch('http://127.0.0.1:8000/api/cameras')
      .then( obj => obj.json() )
      .then( res => commit('setCameras', res))
      },

    fetchLenses({commit}){
      fetch('http://127.0.0.1:8000/api/lenses')
      .then( obj => obj.json() )
      .then( res => commit('setLenses', res))
      },

    fetchFilters({commit}){
      fetch('http://127.0.0.1:8000/api/filters')
      .then( obj => obj.json() )
      .then( res => commit('setFilters', res))
      },

    fetchMicrophones({commit}){
      fetch('http://127.0.0.1:8000/api/microphones')
      .then( obj => obj.json() )
      .then( res => commit('setMicrophones', res))
      },

    fetchTripods({commit}){
      fetch('http://127.0.0.1:8000/api/tripods')
      .then( obj => obj.json() )
      .then( res => commit('setTripods', res))
      },

    setToken({commit}, token){
        commit('setToken', token);
    },

    removeToken({commit}){
      commit('removeToken');
    },

    addToShoppingCart({commit}, price){
      if (this.shoppingCartTotal==null){
        fetch('http://127.0.0.1:8000/api/shopping_cart',{
          method: 'post',
            headers: {'Content-Type': 'application/json',
            'Authorization': `Bearer ${this.state.token}`},
            body: JSON.stringify({price: price})
        })

        commit('setShoppingCart', price);
      }
    },

    setShoppingCart({commit}, price){
      commit('setShoppingCart', price);
    },

    updateShoppingCart({commit}, price){
      commit('updateShoppingCart', price);
    },

    addPurchase({commit}, price){
      fetch("http://127.0.0.1:8000/api/purchases",{
                method: 'post',
                headers: {'Content-Type': 'application/json',
                'Authorization': `Bearer ${this.state.token}`},
                body: JSON.stringify({
                  price: price.toString(),
                  date: new Date().toString(),
                  cameraShopId: 1
                })
            })
    },

    getQuestions({commit}){
      fetch('/questions')
      .then( obj => obj.json() )
      .then( res => commit('setQuestions', res))
    },

    addQuestion({commit}, msg){
      const question = JSON.parse(msg);
      console.log(question.message);
      commit('addQuestionM', { question: question});
    },

    setSocket({commit},socket){
      this.state.socket = socket;
    }

  },
})
